/* harmony default export */ __webpack_exports__["a"] = ({
    name: 'Hero',
    data: function data() {
        return {
            sizeCircle: 0,
            sizeDirection: 0,
            welcomeShow: '',
            settings: {
                circlesLimit: 30,
                extraSpeed: 2
                //                    startY: 0,
                //                    lines: 30,
                //                    maxRadius: 20,
                //                    minRadius: 3,
                //                    startColor: '#FFFFFF',
                //                    middleColor: '#73e47a',
                //                    endColor: '#4d9a4d'
            },

            context: false,
            showedCanvas: false,
            winW: 0,
            winH: 0,
            circles: [],
            mouseDown: false,
            speedChange: false,
            particleCount: 0

        };
    },

    props: ['scrollPosition'],
    created: function created() {
        //            this.initHome();
    },
    mounted: function mounted() {
        this.initHero();
    },

    methods: {
        initHero: function initHero() {
            var $this = this;
            var canvasArea = document.getElementById("canvas-area");
            this.winW = canvasArea.clientWidth;
            this.winH = canvasArea.clientHeight;

            var canvas = document.getElementById("hero__canvas");
            canvas.width = this.winW;
            canvas.height = this.winH;
            console.log(this.winW, this.winH);
            this.context = canvas.getContext("2d");

            this.drawCircles();

            canvas.addEventListener('mousedown', function (event) {
                $this.speedChange = true;
                $this.mouseDown = true;
            }, false);
            canvas.addEventListener('mouseup', function (event) {
                $this.speedChange = true;
                $this.mouseDown = false;
            }, false);
        },

        drawCircles: function drawCircles() {
            var $this = this;
            var randomX = 0,
                randomY = 0,
                speed = 0,
                size = 0;
            for (var i = 0; i < this.settings.circlesLimit; i++) {
                randomX = Math.random() * $this.winW;
                randomY = Math.random() * $this.winH;
                speed = 0.1 + Math.random() * 2;
                size = Math.floor(Math.random() * 50) + 30;
                var circle = new Circle(100, speed, size, randomX, randomY);
                $this.circles.push(circle);
            }

            for (var i = 0; i < 10; i++) {
                randomX = Math.random() * $this.winW;
                randomY = Math.random() * $this.winH;
                speed = 0.1 + Math.random();
                size = 10;
                var _circle = new Circle(100, speed, size, randomX, randomY);
                $this.circles.push(_circle);
            }
            //                for (var i = 0; i < 20; i++) {
            //                    randomX =  Math.random() * $this.winW;
            //                    randomY = Math.random() * $this.winH;
            //                    speed = 0;
            //                    size = 5;
            //                    let circle = new Circle(100, speed, size, randomX, randomY);
            //                    $this.circles.push(circle);
            //                }


            function Circle(radius, speed, width, xPos, yPos) {
                this.radius = radius;
                this.speed = speed;
                this.width = width;
                this.xPos = xPos;
                this.yPos = yPos;
                this.opacity = 0.05 + Math.random() * 0.5;

                this.counter = 0;

                var signHelper = Math.floor(Math.random() * 2);

                if (signHelper == 1) {
                    this.sign = -1;
                } else {
                    this.sign = 1;
                }
            }

            Circle.prototype.update = function () {
                this.counter += this.sign * this.speed;

                this.particleCount++;
                if ($this.speedChange === true) {
                    if ($this.mouseDown === true) {
                        this.counter < 0 ? this.counter -= 2 : this.counter += 2;
                    }
                    if ($this.particleCount >= $this.settings.circlesLimit - 1) $this.speedChange = false;
                }

                if ($this.sizeCircle >= 0.2) {
                    $this.sizeDirection = 1;
                } else if ($this.sizeCircle < 0) {
                    $this.sizeDirection = 0;
                }
                if ($this.sizeDirection === 0) {
                    $this.sizeCircle += 0.00001;
                } else {
                    $this.sizeCircle -= 0.00001;
                }

                $this.context.beginPath();
                $this.context.arc(this.xPos + Math.cos(this.counter / 300) * this.radius, this.yPos + Math.sin(this.counter / 300) * this.radius, this.width, 0, Math.PI * 2, false);
                $this.context.arc(this.xPos + Math.cos(this.counter / 300) * this.radius, this.yPos + Math.sin(this.counter / 300) * this.radius, this.width * (0.8 + $this.sizeCircle), 0, Math.PI * 2, true);

                $this.context.closePath();
                $this.context.fillStyle = 'rgba(115, 228, 122,' + this.opacity + ')';
                $this.context.fill();

                if ($this.particleCount >= $this.settings.circlesLimit - 1) $this.particleCount = 0;
            };

            this.draw();
        },

        draw: function draw() {
            var $this = this;
            this.context.clearRect(0, 0, this.winW, this.winH);
            for (var i = 0; i < $this.circles.length; i++) {
                var myCircle = $this.circles[i];
                myCircle.update();
            }
            if ($this.showedCanvas === false) {
                $this.showedCanvas = true;

                $this.initTitle();
            }
            requestAnimationFrame(this.draw);
        },
        initTitle: function initTitle() {
            console.log("Hero is running");
            this.welcomeShow = 'hero--pre_active';
            setTimeout(function () {
                this.welcomeShow = 'hero--pre_active hero--active';
            }.bind(this), 1200);
        }
    }
});

